import { h } from './element';
import { bindClickoutside, unbindClickoutside } from './event';
import { cssPrefix } from '../config';
import { tf } from '../locale/locale';

let menuItems = [
  { key: 'freezePanes', title: tf('contextmenu.freezePanes'), isShow: false },
  {
    key: 'copy', title: tf('contextmenu.copy'), label: 'Ctrl+C', isShow: false,
  },
  {
    key: 'cut', title: tf('contextmenu.cut'), label: 'Ctrl+X', isShow: false,
  },
  { key: 'paste', title: tf('contextmenu.paste'), label: 'Ctrl+V' },
  {
    key: 'paste-value', title: tf('contextmenu.pasteValue'), label: 'Ctrl+Shift+V', isShow: false,
  },
  {
    key: 'paste-format', title: tf('contextmenu.pasteFormat'), label: 'Ctrl+Alt+V', isShow: false,
  },
  { key: 'divider', isShow: false },
  { key: 'insert-row', title: tf('contextmenu.insertRow'), isShow: false },
  { key: 'insert-column', title: tf('contextmenu.insertColumn'), isShow: false },
  { key: 'divider', isShow: false },
  { key: 'delete-row', title: tf('contextmenu.deleteRow'), isShow: false },
  { key: 'delete-column', title: tf('contextmenu.deleteColumn'), isShow: false },
  { key: 'delete-cell-text', title: tf('contextmenu.deleteCellText'), isShow: false },
  { key: 'divider', isShow: false },
  { key: 'validation', title: tf('contextmenu.validation'), isShow: false },
];

function buildMenuItem(item, isUseDefaultMenu) {
  if (!item.isShow && !isUseDefaultMenu) {
    return false;
  }
  if (item.key === 'divider') {
    return h('div', `${cssPrefix}-item divider`);
  }
  let { title } = item;
  if (typeof item.title === 'function') {
    title = item.title();
  }
  return h('div', `${cssPrefix}-item ${item.key}`)
    .on('click', (e) => {
      if (typeof item.callback === 'function') {
        this.itemClick(item.key, e, item.callback);
      } else {
        this.itemClick(item.key, e);
      }
      this.hide();
    })
    .children(
      title,
      h('div', 'label').child(item.label || ''),
    );
}

function buildMenu(isUseDefaultMenu) {
  // linjian 这里做了筛选，返回false的项不要
  return menuItems.map(it => buildMenuItem.call(this, it, isUseDefaultMenu)).filter(item => item);
}

export default class ContextMenu {
  constructor(viewFn, isHide = false, menuOutItems) {
    let isUseDefaultMenu = true;
    if (menuOutItems && menuOutItems.length !== 0) {
      menuOutItems.some((it) => {
        if (it.isShow) {
          isUseDefaultMenu = false;
          return true;
        }
        return false;
      });
    }
    this.assignItems(menuOutItems);
    menuItems = menuItems.concat(menuOutItems);
    this.el = h('div', `${cssPrefix}-contextmenu`)
      .children(...buildMenu.call(this, isUseDefaultMenu))
      .hide();
    this.viewFn = viewFn;
    this.itemClick = () => {};
    this.isHide = isHide;
  }

  assignItems(menuOutItems) {
    for (let index = 0; index < menuItems.length; index++) {
      const obj = menuItems[index];
      for (let index2 = 0; index2 < menuOutItems.length; index2++) {
        const outObj = menuOutItems[index2];
        if (outObj.key === obj.key) {
          Object.assign(obj, outObj);
          menuOutItems.splice(index2, 1);
        }
      }
    }
  }

  hide() {
    const { el } = this;
    el.hide();
    unbindClickoutside(el);
  }

  // linjian这里添加一个参数处理
  setPosition(x, y, { isFreeze = false }) {
    if (this.isHide) return;
    const { el } = this;
    const { height, width } = el.show().offset();
    const freezePanes = el.el.querySelector('.freezePanes');
    if (freezePanes !== null) {
      if (isFreeze) {
        freezePanes.textContent = tf('contextmenu.unfreezePanes')();
      } else {
        freezePanes.textContent = tf('contextmenu.freezePanes')();
      }
    }
    const view = this.viewFn();
    let top = y;
    let left = x;
    if (view.height - y <= height) {
      top -= height;
    }
    if (view.width - x <= width) {
      left -= width;
    }
    el.offset({ left, top });
    bindClickoutside(el);
  }
}
